.image-list ul {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    box-sizing: border-box;
  }
  
  .image-list ul li {
    list-style: none;
    flex: 0 0 33.333333%;
    padding: 1rem;
  }

.segment {
    position: relative;
    -webkit-box-shadow: 0 1px 2px 0 rgb(34 36 38 / 15%);
    box-shadow: 0 1px 2px 0 rgb(34 36 38 / 15%);
    margin: 1rem 0;
    padding: 0.7em 1em;
    border-radius: .28571429rem;
    border: 1px solid rgba(34,36,38,.15);
    display: flex;
    justify-content: center;
}
.left-segment{
    flex: 0.25 1 0;
    padding: 15px;
    flex-direction: column;
    justify-content: center;
}
.close {
    cursor: pointer;
    position: absolute;
    top: 50%;
    right: 0%;
    padding: 12px 16px;
    transform: translate(0%, -50%);
  }
.right-segment{
    flex: 1 1 0;
    display: flex;
    justify-content: space-around;
    transform: translate(-50% -50%);
}
.form1 {
    font-size: 1rem;
    position: relative;
    max-width: 100%;
}

.form1 .field:last-child, .form1 .fields:last-child .field {
    margin-bottom: 0;
}

.form1 .field {
    clear: both;
    margin: 0 0 1em;
}

.form input:not([type]), .form input[type=date], .form input[type=datetime-local], .form input[type=email], .form input[type=file], .form input[type=number], .form input[type=password], .form input[type=search], .form input[type=tel], .form input[type=text], .form input[type=time], .form input[type=url] {
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    margin: 0;
    outline: 0;
    -webkit-appearance: none;
    tap-highlight-color: rgba(255,255,255,0);
    line-height: 1.21428571em;
    padding: .67857143em 1em;
    font-size: 1em;
    background: #fff;
    border: 1px solid rgba(34,36,38,.15);
    color: rgba(0,0,0,.87);
    border-radius: .28571429rem;
    -webkit-box-shadow: 0 0 0 0 transparent inset;
    box-shadow: 0 0 0 0 transparent inset;
    -webkit-transition: color .1s ease,border-color .1s ease;
    transition: color .1s ease,border-color .1s ease;
}
.form input:not([type]), .form input[type=date], .form input[type=datetime-local], .form input[type=email], .form input[type=file], .form input[type=number], .form input[type=password], .form input[type=search], .form input[type=tel], .form input[type=text], .form input[type=time], .form input[type=url], .form textarea {
    width: 80%;
    vertical-align: top;
}
.ui.container {
    width: auto!important;
    margin-left: 1em!important;
    margin-right: 1em!important;
}
.ui.container {
    display: block;
    max-width: 100%!important;
}

*, :after, :before {
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
}
ol{
    padding: unset;
    height: 180px;
    overflow-y: auto;
    overflow-x: hidden;
    
}
ol li{
    width: 100%;
    position: relative;
    padding: 5px !important;
    margin: 1px;
    background-color: #dde9d7;
}
.close:hover {background: #bbb;}
.btn {
    position: relative;
  
    display: inline-block;
    padding: 11px 11px;
  
    overflow: hidden;
    cursor: pointer;
    border-width: 0;
    outline: none;
    border-radius: 2px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, .6);
    
    background-color: #96b5d1
    ;
    color: #ecf0f1;
    
    transition: background-color .3s;
  }
  
  .btn:hover, .btn:focus {
    background-color: #8cc1f0
    ;
  }
  
  .btn > * {
    position: relative;
  }
  
  .btn span {
    display: block;
    padding: 12px 24px;
  }
  
  .btn:before {
    content: "";
    
    position: absolute;
    top: 50%;
    left: 50%;
    
    display: block;
    width: 0;
    padding-top: 0;
      
    border-radius: 100%;
    
    background-color: rgba(236, 240, 241, .3);
    
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
  
  .btn:active:before {
    width: 120%;
    padding-top: 120%;
    
    transition: width .2s ease-out, padding-top .2s ease-out;
  }
input[type=email], input[type=password], input[type=search], input[type=text] {
    -webkit-appearance: none;
    -moz-appearance: none;
}
button, input {
    overflow: visible;
}
button, input, optgroup, select, textarea {
    font-family: sans-serif;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}

input[type="text" i] {
    padding: 1px 2px;
}

.form {
    font-size: 1rem;
}
.segment, .segments .segment {
    font-size: 1rem;
}

.card {
    background: #fff;
    width: 22rem;
    border: 2px solid #a3a7af ;
    overflow: hidden;
    transition: all 0.2s ease-in-out;
  }
  .card .image-box {
    height: 30vh;
    border-radius: inherit;
    border: none;
    background: #70bee6;
  }
  .card .image-box img {
    width: 100%;
    height: 100%;
  }
  .card .card-content span {
    padding: 0 1.3rem;
  }
  .card .card-title {
    margin-top: 1rem;
    font-size: 1.1rem;
  }

  